import React from "react";
import BootstrapTable from "react-bootstrap-table-next";
const problems = [
  {
    title: "Identity Protected Communication",
    description:
      "Establish identity protected communication channel between two or more parties",
    functional:
      "Maintain anonimity at application level. Even application should be unaware of which 2 users are communicating.",
    scalable:
      "How many user communications can be handled? What are the provisions made so that system can be scaled later on?",
    extensible: "Can the solution be used as a service?"
  },
  {
    title: "DevOps using Bot",
    description:
      "Managing devops using twitter or whatsapp bot (like restart all vms for myApp1)",
    functional: "1. Integrate any social platform 2. working end deployment",
    scalable: "How many commands it can handle simultaneously?",
    extensible:
      "1. Ease of integration with other social media platforms? 2. Ease of integration with other DevOps platforms."
  },
  {
    title: "User system usage analysis",
    description: "Based on usage of application figuring out patterns.",
    functional:
      "1. Based on user application usage data, identify productive hour and distractive hours",
    scalable:
      "1. How much memory it is consuming 2. Minimal or no lag in user's app opening experience",
    extensible: "How easy it is to integrate tracking of a new application."
  }
];
const columns = [
  {
    dataField: "title",
    text: "Title"
  },
  {
    dataField: "description",
    text: "Description"
  },
  {
    dataField: "functional",
    text: "Functional Requirement"
  },
  {
    dataField: "scalable",
    text: "Is it scalable"
  },
  {
    dataField: "extensible",
    text: "Possible Extension / Reuse "
  }
];
const ProblemStatements = () => (
  <BootstrapTable keyField="title" data={problems} columns={columns} />
);
export default ProblemStatements;



